import "./assets/css/index.css";

import ImagesPreviewer from "./components/organism/ImagePreviewer/ImagesPreviewer.vue";
import ImagePreview from "./components/atom/ImagePreview/ImagePreview.vue";
import ImageItem from "./components/atom/ImageItem/ImageItem.vue";
import MacLoader from "./components/atom/MacLoader/MacLoader.vue";
import DropDown from "./components/molecules/DropDown/DropDown.vue";
import DropDownOption from "./components/molecules/DropDownOption/DropDownOption.vue";

export default function install(app) {
    // IMAGE PREVIEWER
    app.component("ImagesPreviewer", ImagesPreviewer);
    app.component("ImagePreview", ImagePreview);
    app.component("ImageItem", ImageItem);

    // HELPERS
    app.component("MacLoader", MacLoader);
    app.component("DropDown", DropDown);
    app.component("DropDownOption", DropDownOption);

    // MODAL
    function toggleModal(show = true) {
        const modal = document.getElementById("my-dropdown");
        if (!show && modal) {
            modal.remove();
        } else if(show && !modal) {
            setTimeout(() => {
                const element = document.createElement("div");
                element.id = "my-dropdown";
                document.getElementById("app")?.appendChild(element);
            })
        }
    }
    app.provide("toggleModal", toggleModal);
}